import { STATUS } from './status';
import { LocalStorage, Utils } from '@common/Utils';

export const GET_PERSONAL_INFO = 'user/GET_PERSONAL_INFO';
export const GET_PERSONAL_INFO_SUCCESS = 'user/GET_PERSONAL_INFO_SUCCESS';
export const GET_PERSONAL_INFO_FAIL = 'user/GET_PERSONAL_INFO_FAIL';
export const ADD_EXCHANGE = 'user/ADD_EXCHANGE';
export const ADD_KEYS = 'user/ADD_KEYS';
export const EDIT_KEYS = 'user/EDIT_KEYS';
export const ADD_PRODUCT = 'user/ADD_PRODUCT';
export const DELETE_PRODUCT = 'user/DELETE_PRODUCT';
export const EDIT_PRODUCT = 'user/EDIT_PRODUCT';
export const USER_LOGIN = 'user/USER_LOGIN';
export const GET_KEYS = 'user/GET_KEYS';
export const GET_KEYS_SUCCESS = 'user/GET_KEYS_SUCCESS';
export const UNAUTHORIZED_SUBSCRIBE = 'user/UNAUTHORIZED_SUBSCRIBE';
export const UNAUTHORIZED = 'user/UNAUTHORIZED';
export const AUTHENTICATE = 'user/AUTHENTICATE';
export const AUTH_PLS = 'user/AUTH_PLS';
export const API_KEY_SUBSCRIBE = 'user/API_KEY_SUBSCRIBE';
export const API_KEY_RESPONSE = 'user/API_KEY_RESPONSE';
export const USER_LOG_OUT = 'user/USER_LOG_OUT';
export const SUBSCRIBE_ON_TRADER = 'user/SUBSCRIBE_ON_TRADER';
export const SUBSCRIBE_ON_TRADER_SUCCESS = 'user/SUBSCRIBE_ON_TRADER_SUCCESS';
export const GET_SUBSCRIBED_PRODUCTS = 'user/GET_SUBSCRIBED_PRODUCTS';
export const GET_SUBSCRIBED_PRODUCTS_SUCCESS =
  'user/GET_SUBSCRIBED_PRODUCTS_SUCCESS';

export const API_KEY_SUBSCRIBE_SUCCESS = 'user/API_KEY_SUBSCRIBE_SUCCESS';
export const UNSUBSCRIBE_TRADER = 'user/UNSUBSCRIBE_TRADER';
export const UNSUBSCRIBE_TRADER_SUCCESS = 'user/UNSUBSCRIBE_TRADER_SUCCESS';
export const GET_MY_PRODUCTS = 'user/GET_MY_PRODUCTS';
export const GET_MY_PRODUCTS_SUCCESS = 'user/GET_MY_PRODUCTS_SUCCESS';
export const BALANCE = 'user/BALANCE';
export const BALANCE_SUCCESS = 'user/BALANCE_SUCCESS';
export const GET_MY_INVESTORS = 'user/GET_MY_INVESTORS';
export const GET_MY_INVESTORS_SUCCESS = 'user/GET_MY_INVESTORS_SUCCESS';
export const SEND_KYC  = 'user/SEND_KYC'
export const SEND_KYC_SUCCESS  = 'user/SEND_KYC_SUCCESS'

const initialState = {
  personalInfo: {
    name: null,
    surname: null
  },
  status: false,
  burses: [],
  products: [],
  investors: [],
  myProducts: [],
  myFollowings: [],
  myIncomeFollowings: [],
};

export const user = (state = initialState, action) => {
  console.log(action.type, action);
  console.log({
    state,
  });

  if (
    action.type.substr(0, 30) === 'GET_INCOME_SUBSCRIBED_PRODUCTS' &&
    action.type.substr(-7) === 'SUCCESS'
  ) {
    let keyId = action.type.substr(31, (action.type.length - 30 - 9)).split('&');
    let { baseBalance, baseIncome } = action.payload.data;

    let nameKey = ((state.burses 
      .find(cur => cur.keyId === keyId[0])) || { name: '{Not filled name}' }).name;

    return {
      ...state,
      myIncomeFollowings: {
        ...state.myIncomeFollowings,
        [keyId[0]]: {
          ...(state.myIncomeFollowings[keyId[0]] || []),
          nameKey,
          [keyId[1]]: {
            baseBalance,
            baseIncome,
            createdAt: keyId[2],
          },
        },
      },
    };
  }

  switch (action.type) {
    case GET_PERSONAL_INFO_SUCCESS:
      if (action.payload.data.error) return state
      return {
        ...state,
        personalInfo: action.payload.data,
        status: 'logged-in',
      };
      break;
    case USER_LOGIN:
      return Object.assign(
        {},
        state,
        {status: STATUS.LOGGED_IN}
      );
    case GET_KEYS_SUCCESS:
      return Object.assign(
        {},
        state,
        {burses: ((action.payload.data.error) ? false : action.payload.data)}
      );
    case API_KEY_RESPONSE:
      return {
        ...state,
        burses: Utils.findBurseAndChangeStatus(action.payload, state.burses)
      };
    case USER_LOG_OUT:
      return {
        ...state,
        ...initialState,
        status: STATUS.UNLOGGED_IN
      };
    case GET_SUBSCRIBED_PRODUCTS_SUCCESS:
      return {
        ...state,
        myFollowings: action.payload.data
      };
    case API_KEY_SUBSCRIBE_SUCCESS:
      return {
        ...state,
        burses: state.burses.map(burse => {
          let result = burse;
          if (burse.stock === action.payload.nameStock) {
            result.status = action.payload.status;
          }
          return result;
        })
      };
    case GET_MY_PRODUCTS_SUCCESS:
      return {
        ...state,
        myProducts: action.payload.data
      };
    case GET_MY_INVESTORS_SUCCESS:
      return {
        ...state,
        investors: action.payload.data
      };
    case SEND_KYC_SUCCESS:
      return state
    case BALANCE_SUCCESS:
      return state;
    default:
      return state;
  }
};

export const logOut = () => {
  return {
    type: USER_LOG_OUT,
  };
};

export const addKeys = (userData) => {
  return {
    type: ADD_KEYS,
    payload: {
      request: {
        method: 'POST',
        url: '/user/key',
        data: userData,
      }
    }
  };
};

export const editAccount = (userData) => {
  return {
    type: EDIT_KEYS,
    payload: {
      request: {
        method: 'PUT',
        url: '/user/key',
        data: userData,
      }
    }
  };
};

export const getKeys = () => {
  return {
    type: GET_KEYS,
    payload: {
      request: {
        url: '/user/key',
        method: 'GET',
      }
    }
  };
};

export const getMyProducts = () => {
  return {
    type: GET_MY_PRODUCTS,
    payload: {
      request: {
        url: 'user/products',
        method: 'GET',
      }
    }
  };
};

export const addProduct = (productData) => {
  return {
    type: ADD_PRODUCT,
    payload: {
      request: {
        method: 'POST',
        url: '/user/products',
        data: productData,
      }
    }
  };
};

export const deleteProduct = (productData) => {
  return {
    type: DELETE_PRODUCT,
    payload: {
      request: {
        method: 'DELETE',
        url: '/user/products',
        data: productData,
      }
    }
  };
};

export const editProduct = (productData) => {
  return {
    type: EDIT_PRODUCT,
    payload: {
      request: {
        method: 'PUT',
        url: `/user/products/${productData.productId}`,
        data: productData,
      }
    }
  };
};

export const getUserInfo = (userData) => {
  return {
    type: GET_PERSONAL_INFO,
    payload: {
      request: {
        method: 'GET',
        url: '/user/info',
        data: userData,
      }
    }
  };
};

export const subscribeOnTrader = (dataFollow) => {
  return {
    type: SUBSCRIBE_ON_TRADER,
    payload: {
      request: {
        method: 'POST',
        url: '/user/follow',
        data: {
          selfKeyId: dataFollow.keyId,
          productId: dataFollow.productId,
        },
      },
    },
  };
};

export const getFollowings = (data) => {
  return {
    type: GET_SUBSCRIBED_PRODUCTS,
    payload: {
      request: {
        method: 'GET',
        url: '/user/followings',
        data: [],
      }
    }
  }
};

export const getIncomeFollowings = (data) => {
  return {
    type: `GET_INCOME_SUBSCRIBED_PRODUCTS_${data.selfKeyId}&${data.baseAsset}&${data.createdAt}`,
    payload: {
      request: {
        method: 'GET',
        url: `/user/income-history/${data.selfKeyId}/${data.baseAsset}`,
        data: [],
      }
    }
  }
};

export const unsubscribeTrader = (dataFollow) => {
  return {
    type: UNSUBSCRIBE_TRADER,
    payload: {
      request: {
        method: 'POST',
        url: '/user/unfollow',
        data: {
          selfKeyId: dataFollow.keyId,
          productId: dataFollow.productId,
        },
      },
    },
  };
};





