import * as React from 'react';
import { PopUpProductPage } from './PopUpProductPage';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
  subscribeOnTrader,
  unsubscribeTrader,
  getSubscribedProducts
} from '@store/modules/user';
import { hidePopUp } from '@store/modules/common';
import { getProducts } from '@store/modules/marketplace';
import {
  getKeys,
  getFollowings,
} from '@store/modules/user';

import { showPopUp } from '@store/modules/common';

import { confirmAlert } from '@common/alertConfirm/main.js';

class PopUpProductPageContainer extends React.Component {
  componentDidMount() {
    const { getKeys, getFollowings, } = this.props;

    getFollowings();
    getKeys();
  }

  render() {
    const { userStocks, myFollowings, showPopUp, } = this.props;

    return (
      <PopUpProductPage
        {...this.props}
        onClickConnect={this.onClickConnect}
        onClickDisconnect={this.onClickDisconnect}
        isNeedHide={this.props.products.indexOf(this.props.id) >= 0}
        isNeedHideDisc={this.props.products.indexOf(this.props.id) < 0}
        userStocks={userStocks}
        myFollowings={myFollowings}
        showPopUpAddKey={() => showPopUp('addApiKeys')}
      />
    );
  }

  onClickConnect = ({
    productId, 
    type,
  }) => {
    const { 
      subscribeOnTrader, 
      getKeys, 
      getFollowings, 
      confirmAlert, 
      userStocks, 
      myFollowings,
    } = this.props;

    const { sendRequestConnect } = this;

    let list = 
      (userStocks || []).map((cur, key) => {
        if (!cur.valid) return { type: 'none', };

        let thisFollowing = myFollowings.some(cur2 => (
          cur2.selfKeyId == cur.keyId
        ))

        let thisFollowingForThisProduct = myFollowings.some(cur2 => (
          cur2.productId == productId &&
          cur2.selfKeyId == cur.keyId
        ))

        if (thisFollowingForThisProduct) {
          return {
            type: 'stop',
            ...cur,
          };
        }

        if (thisFollowing) {
          return {
            type: 'none',
          };
        }

        return {
          type: 'start',
          ...cur,
        };
      });

    if ((
      list.some(cur => cur.type === type)
    )) {
      confirmAlert({
        customUI: ({ onClose }) => {
          return (
            <div className='custom-ui'>
              <p>
                {
                  ({
                    start: `If you really want to connect, select the key.`,
                    stop: `If you really want to disconnect, select the key.`,
                  })[type]
                }
              </p>
              {
                <div className="parentKeysSelect">
                  {
                    list.filter(cur => cur.type === type)
                      .map((cur, index) => (
                        <button key={index} className="itemKeysSelect"
                          onClick={() => {
                            let isConfirm = (window || {confirm:()=>true}).confirm("Are you sure?");
                            if (isConfirm)
                              sendRequestConnect({
                                type,
                                productId,
                                keyId: cur.keyId,
                                stock: cur.stock,
                              }, () => {
                                onClose();
                              });
                          }}
                        >
                          {cur.keyId}
                        </button>
                      ))
                  }
                </div>
              }
              <button onClick={onClose}>No</button>              
            </div>
          );
        }
      });
    }
  }

  sendRequestConnect = (optArr, cb) => {
    const { subscribeOnTrader, unsubscribeTrader, getKeys, getFollowings, } = this.props;

    ({
      start: subscribeOnTrader,
      stop: unsubscribeTrader,
    })[optArr.type]({
        keyId: optArr.keyId, 
        stock: optArr.stock, 
        productId: optArr.productId,
      }).then(()=>{
        getFollowings();
        getKeys();
        cb();
      });
  }

  onClickDisconnect = (productId, keyId, stock) => {
    const { unsubscribeTrader, getKeys, getFollowings, } = this.props;
    console.log("onClickConnect", keyId, stock);

    unsubscribeTrader({keyId, stock, productId}).then(()=>{
      getFollowings();
      getKeys();
    });
  }

  onFinishConnect = () => {
    this.props.getSubscribedProducts();
    this.props.getProducts();
    this.props.hidePopUp();
  }
}

const mapStateToProps = state => {
  return {
    userStocks: state.user.burses,
    myFollowings: state.user.myFollowings,
    personalInfo: state.user.personalInfo,
    products: state.user.products,
    productsManager: state.marketplace.products,
  };
};

const mapDispatchToProps = dispatch => {
  return { 
    ...bindActionCreators({
      unsubscribeTrader,
      subscribeOnTrader,
      hidePopUp,
      getSubscribedProducts,
      getProducts,
      getKeys,
      getFollowings,
      showPopUp,
    }, dispatch),

    confirmAlert,
  }
};

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(PopUpProductPageContainer);

export { connectedContainer as PopUpProductPageContainer };



