import * as React from 'react';

import Highcharts from 'react-highcharts'; 
import HighchartsMore from 'highcharts-more';

// HighchartsMore(Highcharts.Highcharts)

const ROOT_CLASS = 'dashboard-income';

export class DashboardIncome extends React.Component{
	constructor() {
		super();
		
		this.state = {
			activebaseAsset: 'BTC',
			activeTypeShow: 'baseBalance',
		};
	}

	render() {
		const { myIncomeFollowings } = this.props;
		const { activebaseAsset, activeTypeShow } = this.state;

		let seperateIncomes = Object.keys(myIncomeFollowings)
			.filter(keyIncomeArr => !!myIncomeFollowings[keyIncomeArr][activebaseAsset])
			.map(keyIncomeArr => (
				myIncomeFollowings[keyIncomeArr][activebaseAsset]['baseIncome']
					.map((arrThisIncome, index,  thisArr) => ({
						timestamp: new Date(arrThisIncome.timestamp).getTime(),
						value: (arrThisIncome.value / (thisArr[index-1] || {value:1}).value - 1) || 0,
					}))
					.filter((cur, index, arr) => (
						(new Date(cur.timestamp).getTime() >= myIncomeFollowings[keyIncomeArr][activebaseAsset].createdAt)
					))
			))

		let diffIncomes = seperateIncomes
			.map(keyIncomeArr => (
				keyIncomeArr
					.map((arrThisIncome, index,  thisArr) => ({
						timestamp: arrThisIncome.timestamp,
						value: arrThisIncome.value + (thisArr.slice(0, index).reduce((pr, cur) => pr + cur.value, 0))
					}))
			))

		let diffBalances = Object.keys(myIncomeFollowings)
			.filter(keyBalancesArr => !!myIncomeFollowings[keyBalancesArr][activebaseAsset])
			.map((keyBalancesArr, indexKeyForArr) => (
				myIncomeFollowings[keyBalancesArr][activebaseAsset]['baseBalance']
					.filter((cur, index, arr) => (
						(new Date(cur.timestamp).getTime() >= myIncomeFollowings[keyBalancesArr][activebaseAsset].createdAt)
					))
					.map((arrThisBalance, index, thisArr) => ({
						timestamp: new Date(arrThisBalance.timestamp).getTime(),
						value: (arrThisBalance.value - (arrThisBalance.value / (1 + seperateIncomes[indexKeyForArr][index].value))) || 0,
					}))
					.reduce((pr, cur, index, arr) => pr.concat([{
						timestamp: cur.timestamp,
						value: cur.value + (arr.slice(0, index).reduce((pr, cur) => pr + cur.value, 0)),
					}]), [])
			))

		let historyBalances = Object.keys(myIncomeFollowings)
			.filter(keyBalancesArr => !!myIncomeFollowings[keyBalancesArr][activebaseAsset])
			.reduce((pr, keyBalancesArr, indexKeyForArr) => {
				pr[keyBalancesArr] = {
					name: myIncomeFollowings[keyBalancesArr].nameKey,
					data: myIncomeFollowings[keyBalancesArr][activebaseAsset]['baseBalance']
						.filter((cur, index, arr) => (
							(new Date(cur.timestamp).getTime() >= myIncomeFollowings[keyBalancesArr][activebaseAsset].createdAt)
						))
						.map((arrThisBalance, index, thisArr) => ({
							timestamp: new Date(arrThisBalance.timestamp).getTime(),
							value: arrThisBalance.value,//(arrThisBalance.value - (arrThisBalance.value / (1 + seperateIncomes[indexKeyForArr][index].value))) || 0,
						}))
				};
				return pr;
			}, {})

		let showIncomes = Object.keys(myIncomeFollowings)
			.filter(keyIncomeArr => !!myIncomeFollowings[keyIncomeArr][activebaseAsset])
			.map((keyIncomeArr, index) => 
				({
					name: myIncomeFollowings[keyIncomeArr].nameKey,
					data: activeTypeShow === 'baseBalance' ? 
						diffBalances[index]
							.reduce(
								(pr, cur) => pr.concat([[cur.timestamp, cur.value]]), 
								diffBalances[index].length ? [[diffBalances[index][0].timestamp - 86400000, 0]] : []
							) :
						diffIncomes[index]
							.reduce(
								(pr, cur) => pr.concat([[cur.timestamp, cur.value * 100]]), 
								diffBalances[index].length ? [[diffIncomes[index][0].timestamp - 86400000, 0]] : []
							)
				}));

		let nowProfit = showIncomes
			.reduce((pr, cur, index) => pr + ((cur.data && cur.data.length && cur.data[cur.data.length-1][1]) || 0), 0)
			.toFixed(
				({
					'baseIncome': {
						'BTC': 2,
						'USD': 2,
					},
					'baseBalance': {
						'BTC': 5,
						'USD': 2,
					},
				})[activeTypeShow][activebaseAsset]
			);

		let generalDataBalances = Object.keys(historyBalances)
			.reduce((pr, cur) => pr.concat([...historyBalances[cur].data.map(cur2 => cur2.value)]), [])

		let generalData = showIncomes
			.reduce((pr, cur) => pr.concat([cur.data.map(cur2 => cur2[1])]), [])
			.reduce((pr, cur) => pr.concat(cur), []);

		console.log({
			myIncomeFollowings,
			historyBalances,
		});

	  return (
	    <div className={`${ROOT_CLASS}`}>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text`}>Your data for accounts:</div>
	    		<div className={`__button ${activeTypeShow==='baseIncome'}`} onClick={()=>this.setState({activeTypeShow:'baseIncome'})}>in %</div>
	    		<div className={`__button ${activeTypeShow==='baseBalance'}`} onClick={()=>this.setState({activeTypeShow:'baseBalance'})}>in asset</div>
	    		<div className={`__text`}>for:</div>
	    		<div className={`__button ${activebaseAsset==='USD'}`} onClick={()=>this.setState({activebaseAsset:'USD'})}>USD</div>
	    		<div className={`__button ${activebaseAsset==='BTC'}`} onClick={()=>this.setState({activebaseAsset:'BTC'})}>BTC</div>
	    	</div>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text allotted`}>
	    			Profit (total): <span className={`isprofit ${nowProfit>0?'profit':'noprofit'}`}>
	    				{`${nowProfit>0?'+':''}`}{`${nowProfit}${activeTypeShow==='baseBalance'?activebaseAsset:'%'}`}
	    			</span>
	    		</div>
	    	</div>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text`}>History profit:</div>
	    	</div>
	    	<div>
	    		{
	    			(showIncomes.length > 0 && showIncomes[0].data.length) ?
		    			(<Highcharts
		            config={{
		              chart: {
		                type: 'area',
		                backgroundColor: 'rgba(0,0,0,0)',
		              },
		              title: {
		                text: '',
		              },
		              subtitle: {
		                text: document.ontouchstart === undefined ?
		                    'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in',
		              },
		              xAxis: {
		                type: 'datetime',
		                allowDecimals: false,
		              },
		              yAxis: {
		                title: {
		                  text: activeTypeShow === 'baseBalance' ? activebaseAsset : '%',
		                },
		                showFirstLabel: false,
		                showLastLabel: true,
		                min: Math.min(...generalData),
		                max: Math.max(...generalData),
		              },
		              rangeSelector: {
		                selected: 1,
		              },
		              legend: {
		                enabled: true,
		                itemStyle: {
					            color: '#b1b1b1',
					            fontWeight: 'bold'
						        },
		              },
		              credits: {
		                enabled: false,
		              },
		              plotOptions: {
		                area: {
		                  marker: {
		                    radius: 2,
		                  },
		                },
		              },
		              series: [...showIncomes],
		            }}
		          />) : 
		          	<div className={`${ROOT_CLASS}__head`}>
					    		<div className={`__text`}>There is no data on earnings yet, the data is updated by day.</div>
					    	</div>
	      	}
	    	</div>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text`}>History balances:</div>
	    	</div>
	    	<div>
	    		{
	    			(showIncomes.length > 0 && showIncomes[0].data.length) ?
		    			(<Highcharts
		            config={{
		              chart: {
		                type: 'area',
		                backgroundColor: 'rgba(0,0,0,0)',
		              },
		              title: {
		                text: '',
		              },
		              subtitle: {
		                text: document.ontouchstart === undefined ?
		                    'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in',
		              },
		              xAxis: {
		                type: 'datetime',
		                allowDecimals: false,
		              },
		              yAxis: {
		                title: {
		                  text: activebaseAsset,
		                },
		                showFirstLabel: false,
		                showLastLabel: true,
		                min: Math.min(...generalDataBalances),
		                max: Math.max(...generalDataBalances),
		              },
		              rangeSelector: {
		                selected: 1,
		              },
		              legend: {
		                enabled: true,
		                itemStyle: {
					            color: '#b1b1b1',
					            fontWeight: 'bold'
						        },
		              },
		              credits: {
		                enabled: false,
		              },
		              plotOptions: {
		                area: {
		                  marker: {
		                    radius: 2,
		                  },
		                },
		              },
		              series: Object.keys(historyBalances).map(cur => ({
		              	name: historyBalances[cur].name,
		              	data: historyBalances[cur].data.map(cur2 => ([
		              		cur2.timestamp,
		              		cur2.value,
		              	]))
		              })),
		            }}
		          />) : 
		          	<div className={`${ROOT_CLASS}__head`}>
					    		<div className={`__text`}>There is no data on earnings yet, the data is updated by day.</div>
					    	</div>
	      	}
	    	</div>
	    </div>
    )
	 }
};



