import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { TraderCard } from './TraderCard';
import { showPopUp } from '@store/modules/common';
import { deleteProduct } from '@store/modules/user';
import { getProductById } from '@store/modules/marketplace';

import { confirmAlert } from '@common/alertConfirm/main.js';

import {
  getMyProducts,
} from '@store/modules/user';

class TraderCardContainer extends React.Component {
  constructor(props) {
    super(props);  
  }

  render() {
    console.log(555, {
      props: this.props,
    });

    return (
      <TraderCard
        onClick={this.onClickRoot}
        clickEdit={this.clickEdit}
        clickDelete={this.clickDelete}
        edit={this.props.edit || false}
        {...this.props}
      />
    );
  }

  clickEdit = (optArr) => {
    const { showPopUp, } = this.props;

    showPopUp('editProduct', optArr);
  };

  clickDelete = (optArr) => {
    const { 
      confirmAlert, 
      deleteProduct, 
      getMyProducts, 
    } = this.props;

    confirmAlert({
      customUI: ({ onClose }) => {
        return (
          <div className='custom-ui'>
            <p>You want to remove this product.</p>
            <button onClick={onClose}>No</button>   
            <button onClick={() => {
              deleteProduct(optArr);
              setTimeout(getMyProducts, 1000);
              onClose();
            }}>Yes</button>              
          </div>
        );
      }
    });
  };

  onClickRoot = () => {
    const { showPopUp, getProductById, productId, type } = this.props;

    console.log(type);

    getProductById({
      productId,
      type
    })
      .then((res) => {
        const { payload: { data } } = res;

        console.log('onClickRoot', res);

        const propData = {
          ...data,
        };

        showPopUp('productPage', {
          ...propData
        });
      })   
  };
}

const mapStateToProps = state => {
  return {
    history: state.marketplace.historyData
  };
}

const mapDispatchToProps = dispatch => ({
  ...bindActionCreators({
    showPopUp, 
    getProductById, 
    deleteProduct,
    getMyProducts,
  }, dispatch),
  confirmAlert,
})
  

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(TraderCardContainer);

export {connectedContainer as TraderCardContainer};



