import * as React from 'react';
import { ProfilePage } from './ProfilePage';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {
  getPersonalInfo,
  getKeys,
  userLogIn,
  userLogOut,
  getMyProducts,
  getMyInvestors,
  addProduct,
  getUserInfo,
  getFollowings,
  getIncomeFollowings,
  logOut,
} from '@store/modules/user';

import { push } from 'react-router-redux';
import { showPopUp, errorRequests } from '@store/modules/common';
import { LocalStorage } from '@common/Utils';
import { getProducts } from '@store/modules/marketplace';
import { removeTokens } from '@store/modules/registration';
import { objectLangs, lng } from '../../../lngs/index'
import Tour from 'reactour';

class ProfilePageContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: true,
      isTourOpen: false,
    };
  }

  componentWillMount() {
    const { loadProfileInfo } = this;

    loadProfileInfo();
  }

  loadProfileInfo = () => {
    const { getKeys, getMyProducts, getUserInfo, getFollowings } = this.props;
    
    getFollowings();
    getKeys();
    getMyProducts();
    getUserInfo(); 

    getFollowings().then(this.getDashboardIncome);
  }

  getDashboardIncome = () => {
    const { getIncomeFollowings } = this.props;
    let myFollowings = this.props.user.myFollowings;
    let myIncomeFollowing = this.props.user.myIncomeFollowings;

    myFollowings
      .filter(cur => !myIncomeFollowing[cur.follower])
      .map(following => {
        getIncomeFollowings({
          selfKeyId: following.follower,
          baseAsset: 'BTC',
          createdAt: new Date(following.createdAt).getTime(),
        });
        getIncomeFollowings({
          selfKeyId: following.follower,
          baseAsset: 'USD',
          createdAt: new Date(following.createdAt).getTime(),
        });
      });
  }

  midlUserLogOut = () => {
    const { removeTokens, logOut, } = this.props; 

    removeTokens();
    this.props.push("/registration/sign-in");
    logOut();
  }

  render() {
    const { onClickAddProduct, midlUserLogOut } = this;
    const accentColor = '#5cb7b7';

    return (
      <div>
        <ProfilePage
          isLoaded={this.state.isLoaded}
          isShowedPopUpNewProduct={this.state.isShowedPopUpNewProduct}
          user={this.props.user}
          push={this.props.push}
          userLogOut={midlUserLogOut}
          products={this.props.user.myProducts}
          keys={this.props.user.burses}
          myIncomeFollowings={this.props.user.myIncomeFollowings}
          edit={true}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.user
  };
};

const mapDispatchToProps = dispatch => {
  return { 
    ...bindActionCreators({
      getPersonalInfo,
      getKeys,
      push,
      showPopUp,
      userLogIn,
      userLogOut,
      getProducts,
      getMyProducts,
      getMyInvestors,
      addProduct,
      getUserInfo,
      getFollowings,
      getIncomeFollowings,
      logOut,
    }, dispatch),

    errorRequests,
    removeTokens
  }
}

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(ProfilePageContainer);

export {connectedContainer as ProfilePageContainer};



