import * as React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { getProductById } from '@store/modules/marketplace';
import { showPopUp } from '@store/modules/common';

const ROOT_CLASS = 'api';

const status = {
  false: 'Invalid',
  true: 'Valid'
}

class Api extends React.Component {
  onClickRoot = (productId) => {
    const { showPopUp, getProductById } = this.props;
    const filterBalancesForZero = (balancesArr) => balancesArr.filter((cur, key) => cur.available > 0);

    getProductById({
      productId,
      type: 'marketplace'
    })
      .then((res) => {
        console.log({
          res,
        });
        const { payload: { data } } = res;
        const propData = {
          ...data,
          // balances: data.balances ? filterBalancesForZero(data.balances) : []
        };

        showPopUp('productPage', {
          ...propData
        });
      })   
  };

  clickEditKey = (keyId) => {
    const { showPopUp, } = this.props;

    showPopUp('editApiKeys', {
      keyId
    });
  };

  render() {
    const { clickEditKey } = this;
    const { user: { myFollowings, }, keyId, edit, name, } = this.props;

    console.log(777, {
      name: this.props.name,
    });

    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__head`}>
          <div className={`${ROOT_CLASS}__exchange ${ROOT_CLASS}__exchange_${this.props.stock}`}></div>
          <div className={`${ROOT_CLASS}__rigthBlock`}>
            <div className={`${ROOT_CLASS}__status ${ROOT_CLASS}__status_${this.props.valid?'valid':'error'}`}>
              { status[this.props.valid] }
            </div>
            {
              edit && (
                <React.Fragment>
                  <div className={`${ROOT_CLASS}__ButtonEditApi`} onClick={clickEditKey.bind(this, keyId)}>edit</div>
                  {
                    //<div className={`${ROOT_CLASS}__ButtonDeleteApi`}>delete</div>
                  }
                </React.Fragment>
              )
            }
          </div>
        </div>
        {
          myFollowings
            .filter(cur => cur.stoppedAt === -1)
            .filter(cur => cur.follower == this.props.keyId)
            .map((cur, key) => (
              <div key={key} className={`${ROOT_CLASS}__followsBlock`}>{(name||'{Not filled name}')} folows for 
                <span onClick={()=>this.onClickRoot(cur.leader)}> Product({cur.leader}) </span>
              </div>
            ))
        }
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.user
  };
};

const mapDispatchToProps = dispatch =>
  bindActionCreators({showPopUp, getProductById}, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(Api);

export {connectedContainer as Api};



