import * as React from 'react';
import { Utils } from '@common/Utils';
import { User } from '@common/User';
import { Unit } from '@common/Unit';

import Highcharts from 'react-highcharts'; 
import HighchartsMore from 'highcharts-more';

import { Link } from 'react-router-dom';

import { objectLangs, lng } from '../../../../lngs/index';

HighchartsMore(Highcharts.Highcharts)

const ROOT_CLASS = 'product-page';

export class PopUpProductPage extends React.Component {
  domForDeletedProduct() {
    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__forDeletedProduct`}>
          This product does not exist, it may have been deleted by its creator.
        </div>
      </div>
    );
  };

  render() {
    const { props, domForDeletedProduct, } = this;
    const { userStocks, myFollowings, showPopUpAddKey, } = props;

    let {
      rating,
      trader,
      balances,
      name,
      stock,
      baseAsset,
      description,
      productId,
    } = props || [];

    let {
      allTimeHigh,
      baseIncomeHistory,
      incomeAverage,
      maxDrawdown,
      maxRecoveryTimeShare,
      value,
      baseBalanceHistory,
      nTicks,
      volatilityState,
    } = rating || [];

    if (!productId) {
      return domForDeletedProduct(); 
    }

    let volatilitySqr = () => {
      const correctedN = Math.max(1, nTicks - 2);
      return (
        volatilityState.sqrSum
        - (volatilityState.linSum ** 2) / correctedN
      ) / correctedN;
    }

    if (baseIncomeHistory && baseIncomeHistory.length > 0) {
      baseIncomeHistory = baseIncomeHistory
        .filter((cur, index, arr) => {
          return (cur.value !== 1 || (arr[index+1] && arr[index+1].value !== 1));
        });
    }

    let polarItems;
    let polarItemsChart;
    let income;
    let baseIncomeHistoryToPercent
    let stepes = {
      "MaxDrawdown": [
        0,
        5,
        15,
        45,
        80,
        100,
        
      ],
      "DmaxDD": [
        0,
        5,
        15,
        45,
        80,
        100,
      ],
      "IncomeAverage": [
        -100,
        0.00001,
        0.005,
        0.09,
        0.5,
        1.5,
      ],
      "Volatility": [
        0,
        0.01,
        0.1,
        0.5,
        1.0,
        2.5,
      ],
      "Income": [
         0,
        5,
        15,
        45,
        80,
        100,
      ],
    };

    let getStepPoint = (index, value) => {
      return stepes[index].reduce((pr, cur, index2, arr) => {
        return pr === false ? ((value >= cur && value <= (arr[index2+1] || Infinity)) ? index2 : false) : pr;
      }, false) || 0;
    };

    if (Object.keys(rating).length > 0 && baseIncomeHistory.length) {
      income = baseIncomeHistory[baseIncomeHistory.length-1].value / baseIncomeHistory[0].value - 1;
      polarItems = {
        "Volatility": volatilitySqr() * 10,
        "IncomeAverage": (incomeAverage < 0 ? 0 : incomeAverage),
        "MaxDrawdown": maxDrawdown,
        "DmaxDD": maxRecoveryTimeShare,
        "Income": (income < 0 ? 0 : income),
      };

      polarItems = Object.keys(polarItems)
        .reduce((pr, cur) => (pr[cur] = (polarItems[cur] * 100).toFixed(2), pr)
        , {});

      polarItemsChart = Object.keys(polarItems).map((cur, index, arr) => 
        (getStepPoint(cur, polarItems[cur]) * 10) + 10
      )
    }

    if (baseIncomeHistory && baseIncomeHistory.length > 0) {
      baseIncomeHistoryToPercent = baseIncomeHistory
        .map((cur, index) => {
          return {
            ...cur,
            value: (cur.value * 100) - 100,
          }; 
        });
    }

    let list = 
      (userStocks || []).map((cur, key) => {
        if (!cur.valid) return { type: 'none', };

        let thisFollowing = myFollowings.some(cur2 => (
          cur2.selfKeyId == cur.keyId
        ))

        let thisFollowingForThisProduct = myFollowings.some(cur2 => (
          cur2.productId == productId &&
          cur2.selfKeyId == cur.keyId
        ))

        if (thisFollowingForThisProduct) {
          return {
            type: 'stop',
            ...cur,
          };
        }

        if (thisFollowing) {
          return {
            type: 'none',
          };
        }

        return {
          type: 'start',
          ...cur,
        };
      });

    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__user-block`}>
          <User
            nameProduct={name}
            theme="small"
            rating={rating}
            block="productPage"
            stock={stock}
          />
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Trader:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{trader.firstName} {trader.lastName}</div>
        </div>  
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Name product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{name}</div>
        </div>
        {
          rating && Object.keys(rating).length > 0 && !isNaN(rating.value) && Object.keys(polarItems || {}).length ?
            <React.Fragment>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating metrics:</div>
              </div>
              <div className={`${ROOT_CLASS}__ratingvars`}>
                <div className={`${ROOT_CLASS}__ratingvars__left`}>  
                  {<Highcharts 
                    config={{
                      chart: {
                        polar: true,
                        type: 'line',
                        backgroundColor: 'rgba(0,0,0,0)',

                        height: 200
                      },
                      title: {
                        text: null
                      },
                      pane: {
                        size: '80%'
                      },
                      xAxis: {
                        categories: Object.keys(polarItems),
                        tickmarkPlacement: 'on',
                        lineWidth: 0,
                      },
                      yAxis: {
                        gridLineInterpolation: 'polygon',
                        lineWidth: 0,
                        title: {
                          text: '',
                          align: 'high',
                        },
                        labels:{
                          enabled:false,
                        },
                      },
                      tooltip: {
                        shared: false,
                      },
                      tooltip: { enabled: false },
                      legend: {
                        enabled: false
                      },   
                      credits: {
                        enabled: false
                      }, 
                      plotOptions: {
                        series: {
                          fillColor: 'red',
                        },
                      },    
                      series: [{
                        data: polarItemsChart,
                        pointPlacement: 'on',
                      }],
                    }}
                  />}
                </div>
                <div className={`${ROOT_CLASS}__ratingvars__right`}>
                  {
                    Object.entries(polarItems).map((cur, key) =>  
                      <div className={`${ROOT_CLASS}__ratingvars__right_item`} key={key}>
                        <span>{cur[0]}</span> : <span>{cur[1]}%</span>
                      </div>
                    ) 
                  }
                </div>
              </div>
            </React.Fragment>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>N/A</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Base asset:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{baseAsset}</div>
        </div>
        {
          (baseIncomeHistoryToPercent && baseIncomeHistoryToPercent.length > 0 && typeof baseIncomeHistoryToPercent === "object") ?
            <div className={`${ROOT_CLASS}__incomechart`}>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>
                  Income at this moment:
                </div>
                <div className={`${ROOT_CLASS}__infoblockcontent`}>
                  {`${baseIncomeHistoryToPercent[baseIncomeHistoryToPercent.length-1].value.toFixed(2)}%`}
                </div>
              </div> 
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Income in history:</div>
              </div>
              <Highcharts
                config={{
                  chart: {
                    type: 'area',
                    backgroundColor: 'rgba(0,0,0,0)',
                  },
                  title: {
                    text: ''
                  },
                  subtitle: {
                    text: document.ontouchstart === undefined ?
                        'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in'
                  },
                  xAxis: {
                    type: 'datetime',
                    allowDecimals: false,
                  },
                  yAxis: {
                    title: {
                      text: '%'
                    },
                    showFirstLabel: false,
                    showLastLabel: true
                  },
                  rangeSelector: {
                    selected: 1
                  },
                  legend: {
                    enabled: false
                  },
                  credits: {
                    enabled: false
                  },
                  plotOptions: {
                    area: {
                      marker: {
                        radius: 2
                      },
                    }
                  },
                  series: [{
                    name: `Income in ${baseAsset} for percent`,
                    data: baseIncomeHistoryToPercent.map(cur => [new Date(cur.timestamp).getTime(), cur.value]),
                  }]  
                }}
              />
            </div>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Income:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>No data yet</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Description product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{description}</div>
        </div>
        <div className={`${ROOT_CLASS}__footer-block`}>
          {
            ([
              ['stop', list.some(cur => cur.type === 'stop')],
              ['start', list.some(cur => cur.type === 'start')],
            ]).map((cur, index) => (
              ((cur[1] && { 
                stop: <div 
                  key={index} 
                  className={`${ROOT_CLASS}__footer-button stop`}
                  onClick={props.onClickConnect.bind(null, {
                    productId,
                    type: 'stop',
                  })}
                >Stop following</div>, 

                start: <div 
                  key={index} 
                  className={`${ROOT_CLASS}__footer-button`}
                  onClick={props.onClickConnect.bind(null, {
                    productId,
                    type: 'start',
                  })}
                >Start following</div>, 
              }) 
              || [])[cur[0]] || null
            ))
          }

          {
            (
              userStocks === false ? 
                <Link to={'/registration/sign-up'}>
                  <div className={`${ROOT_CLASS}__footer-button`}>Sign up and follow</div> 
                </Link>
              : 
                (typeof userStocks === 'object' && userStocks.length === 0) ? 
                  <Link to={'/profile'} onClick={showPopUpAddKey}>
                    <div className={`${ROOT_CLASS}__footer-button`}>Add account and follow</div> 
                  </Link>
              :
                false
            ) || null
          }
        </div>
      </div>
    )
  }
}



